.template-card{
    // background-color: red;
    .template-header{
        display: flex;
        height: 14px;
        background-color: #ebedef ;
        flex-direction: row;
        align-items: center;
        padding-inline-start: 6px ;
        gap: 4px;
        & .circle{
            width: 4px;
            height: 4px;
            border-radius: 50%;
            background-color: rgba(255, 255, 255, 0.937);
        }

        
    }
    .template-card-img-container{
        position: relative;

        
        .template-card-img{
            aspect-ratio: 16/9;
            width: 100%;
            &.smoke{
                opacity: 0.16;
            }
        }
    }
    .card-hover-screen{
        position:absolute ;
        left: 0;
        top: 0;
        display: flex;
        width: 100%;
        height: 100%;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        row-gap: 1em;
        & button{
            display: flex;
            align-items: center;
            justify-content: center;
            width: rem(95px);
            height: rem(48px);
            border-radius: 24px;
            position: relative;
            z-index: 10;
            font-weight: 400;
            font-size: 15px;
            letter-spacing: 1px;
            
            &.edit{
                background: rgb(12, 110, 252);
                color: #fff;
                border: none;
                &:hover{
                   background-color:  lighten(rgb(12, 110, 252),10%);
                }
            }
            &.view{
                border: 1px solid #0c6efc ;
                color: #0c6efc;
                &:hover{
                    background: #0c6efc;
                    color: #fff;
                }
            }
        }
    }
}



